$font-title-size: 1.85rem;
$font-title-spacing: 0.90 * $font-title-size;
$font-description-size: 1.30rem;
$font-description-spacing: 0.95 * $font-description-size;

#banners {
  margin-bottom: $red_space;

  .banner {
    width: 100%;

    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: $purple_space;
    margin-bottom: $purple_space;
    height: $carousels_height;
  }

  .card,
  .card .content,
  .cover {
    border-radius: $card_radius;
  }

  .cover {
    object-fit: cover;
    object-position: center;
    height: $carousels_height;
    width: 100%;
  }

  .card {
    position: relative;
    padding: 1.5 * $blue_space;

    @media screen and (max-width: map.get($screens, "medium")) {
      padding: $blue_space;

      br {
        display: none;
      }
    }

    .title {
      display: block;
      font-weight: 800;
      font-size: $font-title-size;
      line-height: $font-title-size;
      margin-bottom: $blue_space;
    }

    .description {
      display: block;
      margin-bottom: $blue_space;
      line-height: $font-description-spacing;
      font-size: $font-description-size;
    }

    .content {
      margin: 0;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);

      @media screen and (max-width: map.get($screens, "medium")) {
        position: relative;
      }
    }

    .rounded-button {
      display: inline-block;

      @media screen and (max-width: map.get($screens, "medium")) {
        width: 100%;
      }
    }
  }


  @media screen and (max-width: map.get($screens, "mobile")) {
    .banner {
      grid-template-columns: 1fr 0fr;
      gap: 0;
      height: auto;
    }

    .cover {
      display: none;
    }

    .card {
      padding: 0;
      background-size: cover;
      border-style: none;
      // background image is being set in
      // the generated file banners.css
    }

    .content {
      width: 100%;
      height: 100%;
      padding: $blue_space;
      backdrop-filter: brightness(50%) blur(6px);
      -webkit-backdrop-filter: brightness(50%) blur(6px);
      color: white;
    }

    .rounded-button {
      color: white;
      border-color: white;
      padding-left: 0.5rem;
      padding-right: 0.5rem;
    }

    .rounded-button:hover {
      background-color: white;
      color: black;
    }
  }
}

