.card-e, .card {
    padding: 1rem;
    border-style: none !important;
}

.card-e .title {
    display: block;
    font-weight: 800;
    font-size: $font-title-size;
    line-height: $font-title-size;
    margin-bottom: $blue_space;
}

.card-e .description {
    display: block;
    margin-bottom: $blue_space;
    line-height: $font-description-spacing;
    font-size: $font-description-size;
}

.card-e .rounded-button {
    display: inline-block !important;
    display: inline-block;
}