.dark .filled,
.filled.dark {
    background-color: black;
    color: white;
}

.dark .filled:hover,
.filled.dark:hover {
    background-color: #292929;
}

.light .filled,
.filled.light {
    background-color: white;
    color: black;
}

.light .filled:hover,
.filled.light:hover {
    background-color: #DCE0DE;
}

.outlined {
    border: solid;
    border-width: 0.15rem;
    background-color: transparent;
}

.dark .outlined,
.outlined.dark {
    border-color: black;
    color: black;
}

.dark .outlined:hover,
.outlined.dark:hover {
    background-color: black;
    color: white;
}

.light .outlined,
.outlined.light {
    border-color: white;
    color: white;
}

.light .outlined:hover,
.outlined.light:hover {
    background-color: white;
    color: black;
}

.grey .filled,
.filled.grey {
    background-color: #a6a6a6;
    color: white;
}

.grey .filled:hover,
.filled.grey:hover {
    background-color: #292929;
}

.grey.text,
.text.grey {
    color: #a6a6a6;
}