.gallery {
  position: relative;

  width: 100%;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr;

  gap: $purple_space;

  @media (max-width: map.get($screens, "mobile")) {
    grid-template-columns: 1fr;
  }

  .item-card {
    width: 100%;
    height: 0;
    padding-bottom: 100%;
    position: relative;
    background-color: gray;

    .hidden-preview {
      opacity: 0%;
      width: 100%;
      height: 100%;
      color: white;
      transition: $default_trans_interval;

      .title {
        position: absolute;
        float: left;
        top: 50%;
        left: 50%;
        text-align: center;
        transform: translate(-50%, -50%);
        line-height: 100%;

        font-family: 'Tilt Warp', sans-serif;
        font-size: 4rem;

        hyphens: manual;

        @media screen and (max-width: map.get($screens, "medium")) {
          font-size: 3rem;
        }
      }

      .title-holder {
        position: absolute;
        background-color: rgba(0, 0, 0, 0.5);
        width: 100%;
        height: 100%;
      }
    }

    .hidden-preview:hover {
      opacity: 100%;
      background-color: gray;
      position: absolute;
    }

    .cover {
      width: 100%;
      height: 100%;
      position: absolute;
    }

    img {
      object-fit: cover;
      object-position: center;
      height: 100%;
      width: 100%;
    }
  }
}

