$change_carousel_colors_on: "mobile";

#main-carousel {
    position: relative;

    width: 100%;
    height: 35.38vw;

    @media screen and (max-width: map.get($screens, "medium")) {
        width: 100%;
        height: 80vh;
    }
}

.slides .page {
    display: none;
    width: 100%;
    height: 100%;
}

.slides .page.active {
    display: block;
}

.slides .page img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;

    @media screen and (max-width: map.get($screens, "mobile")) {
        object-position: left;
    }
}

.slides .page .textual {
    position: absolute;
    width: 100%;
    height: 100%;

    transition: 0.2s;
}

#main-carousel .page .textual .content {
    position: absolute;
    top: 50%;
    left: 15%;
    transform: translate(0%, -50%);

    @media screen and (max-width: map.get($screens, "medium")) {
        left: unset;
        padding: 0 2rem;
        transform: translateY(-50%);
    }

    @media screen and (max-width: map.get($screens, "mobile")) {
        left: unset;
        position: absolute;
        width: 100%;
        padding: 0 2rem;
        transform: translateY(-50%);
    }
}

#main-carousel .page .textual .title {
    font-size: 3.3rem;
    font-weight: bold;
    line-height: 100%;
    margin-bottom: $purple_space;
}

#main-carousel .page .textual .description {
    font-size: 1.3rem;
    font-weight: 400;
    line-height: 90%;
    margin-bottom: 1.3 * $purple_space;
}

#main-carousel .page .textual br {
    @media screen and (max-width: map.get($screens, "mobile")) {
        display: none;
    }
}