@use "sass:map";
@import url('https://fonts.googleapis.com/css2?family=Mukta:wght@200;400;500;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mukta:wght@400;700;800&family=Tilt+Warp&display=swap');

@import "values";
@import "pallete";

@import "gallery";
@import "main-carousel";
@import "rounded-buttons.scss";
@import "blog-carousel";
@import "blog";
@import "navbar";
@import "footer";
@import "main-card-section";
@import "post";
@import "card";
@import "about";
@import "home/more";
@import "contact";
@import "project";


body {
  margin: 0;
}

.m-default {
  margin-left: $red_space;
  margin-right: $red_space;

  @media screen and (max-width: 1000px) {
    margin-left: calc($red_space * 0.5);
    margin-right: calc($red_space * 0.5);
  }
}

.bold {
  font-weight: bold;
}

h1,
h2,
h3,
h4,
h5,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
.nav-link,
.nav-brand,
div,
span,
a,
article {
  font-family: 'Mukta';
}

.dark {
  color: black;
}

.light {
  color: white;
}

.section-title {
  padding: (($red_space - $section_title_font_size) * .5) 0;
  font-size: $section_title_font_size;
  font-weight: bold;
  line-height: $section_title_font_size;
  vertical-align: middle;
}

@keyframes opacity-decrease {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes opacity-increase {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
