$article-font-size: 1.5rem;
$article-main-title: 2 * $article-font-size;

article.post section {
    font-size: $article-font-size;
}

article.post {
    max-width: 50rem;
    margin: auto;
    padding: 1rem;
}

article.post header {
    width: 100%;
}

article.post .mininav,
article.post .mininav a {
    color: map.get($colors, "onPrimaryOp5");
    text-decoration: none;
}

article.post .author {
    font-weight: bold;
}

article.post header .metadata {
    margin: 1rem 0;
    padding: 1rem 0;
    border-top: solid;
    border-bottom: solid;
    color: map.get($colors, "onPrimaryOp5");
}

article.post header h1 {
    font-weight: 800;
    font-size: $article-font-size !important;
    margin: 1.5rem 0;
}

article.post header figure {
    margin: 0;
    width: 100%;
    color: map.get($colors, "onPrimaryOp5");
    text-align: center;
}

article.post h1 {
    font-weight: 700;
}

article.post img {
    width: 100%;
}

blockquote {
    margin: 1rem;
    padding: 0 1.1rem;
    border-left: solid 0.2rem;
    font-size: 1.25 * $article-font-size;
    background-color: rgb(234, 234, 234);

    font-weight: 600;
    font-style: italic;
}

blockquote blockquote {
    border-left: none;
}

article.post section h1,
article.post section h2,
article.post section h3,
article.post section h4,
article.post section h5,
article.post section h6 {
    margin-top: 1.5rem;
    font-weight: 500;
}

article.post .card-e {
    width: 50%;
    padding-left: 0 !important;
    margin-top: $red_space;
    @media screen and (max-width: map.get($screens, "mobile")) {
        width: 100%;
        margin-top: .75 * $red_space;
    }
}