.project-body {
  width: 100%;
  background-color: #f0b30a;

  img {
    width: 100%;
  }

  p {
    @extend .m-default;
    margin: 1rem;
  }

  p:has(img) {
    margin: 0 0 0 0;
  }

  .project-header {
    padding: $red_space;
  }

  .card-e {
    margin-left: $red_space;
  }
}

#projects-section,
.project-body{
  .card-e {
    margin-top: $red_space;
    width: 50%;
    @media screen and (max-width: map.get($screens, "mobile")) {
      width: 100%;
      margin-top: 0.75 * $red_space;

      br {
        display: none;
      }
    }
  }
}
