.topbar,
.topbar.expanded,
.topbar .content,
.topbar.expanded .content,
.topbar .drawer,
.topbar .item.route,
.topbar.expanded .brand {
  transition: $default_trans_interval;
}

.topbar {
  padding-top: 1rem;
  padding-bottom: 1rem;

  $brand_height: .95 * $blue_space;

  .content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .drawer {
    display: none;
    color: map.get($colors, "onPrimary");
    font-size: $icon_font_size;
    line-height: $brand_height;
  }

  .brand,
  .leading,
  .trailing {
    display: inline-block;
    height: $brand_height;
  }

  .brand {
    height: $brand_height;

    .brand-img {
      height: 100%;
    }
  }

  .leading {
    .item.route {
      text-decoration: none;
      line-height: $blue_space;

      padding-right: 1rem;

      color: map.get($colors, "onPrimary");
      transition: $default_trans_interval;
    }

    .item.route:hover {
      color: map.get($colors, "onPrimaryOp5")
    }

    .item.route.active {
      font-weight: 800;
    }
  }

  .trailing {
    .item.social {
      position: relative;
      font-size: $icon_font_size;
      text-align: center;

      display: inline-block;

      margin: 0.1rem;

      width: $blue_space;
      height: $blue_space;
      padding: 0;
    }

    .item.social i {
      padding-top: 0;
      padding-bottom: 0;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  @media screen and (max-width: map.get($screens, "medium")) {
    .leading,
    .trailing {
      display: none;
    }

    .drawer {
      display: block;
    }

    .content {
      margin-left: 1rem;
      margin-right: 1rem;

      .brand {
        margin: auto;
      }
    }
  }

  @media screen and (max-width: map.get($screens, "mobile")) {
    $brand_height: $brand_height * .75;

    .content {
      .brand {
        height: $brand_height;
      }
    }
  }
}

.topbar.expanded {
  @media screen and (max-width: 900px) {
    .content {
      display: block;
    }

    .brand {
      display: none;
    }

    .leading,
    .trailing {
      display: block;
      height: auto;
    }

    .leading .item.route {
      text-decoration: none;
      display: block;
      padding-bottom: 1rem;
      padding-top: 1rem;
    }
  }
}