#blog-carousel .carousel-item {
    position: relative;
}

#blog-carousel .textual-holder {
    position: absolute;

    width: 100%;
    height: 100%;

    background-color: rgba(0, 0, 0, 0.5);
}

#blog-carousel .textual {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

#blog-carousel .textual .title {
    font-size: 3.5rem;
    font-weight: bold;
    line-height: 100%;
    margin-bottom: $blue_space;
}


#blog-carousel .carousel-item {
    height: $carousels_height;
}

#blog-carousel .carousel-item img {
    object-fit: cover;
    object-position: center;
    height: 100%;
    width: 100%;
    margin-bottom: 1rem;
}

#blog-carousel .carousel-control-prev,
#blog-carousel .carousel-control-next {
    width: 5%;
}