.rounded-button {
    text-decoration: none;
    text-align: center;

    border-radius: 5rem;

    font-weight: bold;
    font-size: 1.3rem;

    padding: 0.2rem 2.5rem;

    transition: $default_trans_interval;
}

.rounded-button.icon {
    position: relative;
    font-size: $icon_font_size;
    text-align: center;

    display: inline-block;

    margin: 0.1rem;

    width: $blue_space;
    height: $blue_space;
    padding: 0;
}

.rounded-button.icon i {
    padding-top: 0;
    padding-bottom: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}