$about_header_height: 5 * $red_space;
$about_header_title_font_size: 3.5rem;
$about_step_font_size: 1.2rem;
$about_step_line_height: 1.2rem;
$about_step_description_font_size: 1rem;

.about-section {
    margin: 2rem (3 * $red_space);

    .content {
        .list {
            margin: 0 6rem;

            @media screen and (max-width: 900px) {
                margin: 0;
            }
        }
    }

    @media screen and (max-width: 1200px) {
        margin: 2rem (2 * $red_space);
    }


    @media screen and (max-width: 900px) {
        margin: 1rem (0.25 * $red_space);
    }

    counter-reset: about_count;
}

.about-section .card-e {
    padding-left: 0;
    padding-right: 0;
}

.about-section .content .card-e {
    margin-top: $red_space;
    width: 50%;
    @media screen and (max-width: map.get($screens, "mobile")) {
        width: 100%;
        margin-top: 0.75 * $red_space;
    }
}

/* HEADER */
.about-section .header {
    display: flex;
    align-items: center;
    gap: 2rem;
    margin-bottom: 3rem;

    @media screen and (max-width: 900px) {
        flex-direction: column-reverse;
        gap: 1rem;
    }
}

.about-section .header .card-e .title {
    font-size: $about_header_title_font_size;
    line-height: $about_header_title_font_size;

    @media screen and (max-width: map.get($screens, "mobile")) {
        font-size: .8 * $about_header_title_font_size;
    }
}

.about-section .header figure {
    padding: 0;
    margin: 0;
}

.about-section .header .cover img {
    border-radius: $card_radius;

    height: $about_header_height;
    min-width: $about_header_height;
    width: $about_header_height;

    @media screen and (max-width: map.get($screens, "mobile")) {
        max-width: 100%;
    }

    object-fit: cover;
    object-position: center;
}

.about-section .header .cover figcaption {
    display: none;
}

/* END HEADER */

.about-section .content .list {
    padding-bottom: $blue_space 0;
}

.about-section .content .list .item {
    display: flex;
    margin-top: $red_space;
}

.about-section .content .list .item .title {
    font-weight: bold;
    font-size: $about_step_font_size;
    line-height: $about_step_line_height;
}

.about-section .content .list .item .description {
    margin-bottom: 0;
}

.about-section .content .item:before {
    color: map.get($colors, "secondary");

    font-size: $red_space;
    line-height: $red_space;
    font-weight: 800;

    margin-right: 0.5rem;

    transform: translateY(-0.5 * $red_space);

    content: "0" counter(about_count);
    counter-increment: about_count;

    @media screen and (max-width: map.get($screens, "mobile")) {
        font-size: $red_space * .5;
        line-height: $red_space * .5;
        transform: translateY(0);
    }
}