.blog-post {
    position: relative;
    height: $carousels_height;  
    margin-bottom: $blue_space;
}

.blog-post a {
    text-decoration: none;
}

.blog-post .textual-holder {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    transition: $default_trans_interval;

    border-radius: $default_radius;
}

.blog-post .textual-holder:hover {
    background-color: rgba(0, 0, 0, 0.25);
}

.blog-post .textual {
    position: relative;

    top: 50%;
    left: $red_space;
    transform: translate(0%, -50%);
}

.blog-post .textual .title {
    font-size: 40pt;
    font-weight: bold;
    line-height: 100%;
    margin-bottom: $blue_space;
}

.blog-post img {
    object-fit: cover;
    object-position: center;
    height: 100%;
    width: 100%;
    margin-bottom: 1rem;
    border-radius: $default_radius;
}

